import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/pages/HomeView.vue'
import Dicionario from '../views/pages/dicionario.vue'
import Aula from '../views/pages/Aula.vue'
import Biblioteca from '../views/pages/Biblioteca.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect:"/home"
  },
  {
    path: '/home',
    name: 'home',
    component: HomeView
  },
  {
    path: '/dicionario',
    name: 'dicionario',
    component: Dicionario
  },
  {
    path: '/biblioteca',
    name: 'biblioteca',
    
    component: Biblioteca,
    children:[
     
        {
          path:'livros',
          name:'livros',
          component: ()=>import('../views/pages/Biblioteca/Livro.vue')
          
        },
        {
          path:'videos',
          name:'videos',
          component: ()=>import('../views/pages/Biblioteca/Video.vue')
          
        },
        {
          path:'audios',
          name:'audios',
          component: ()=>import('../views/pages/Biblioteca/Audio.vue')
          
        },
        {
          path:'descarregados',
          name:'descarregados',
          component: ()=>import('../views/pages/Biblioteca/Descarregado.vue')
          
        }
    ]
  },
  {
    path: '/aulas',
    name: 'aulas',
    component: Aula
  },

]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
})

export default router
